.App-header {
    background-color: #282c34;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(4vmin);
    color: white;
    height: 10vh;
    width: 100%;
} 

.Header-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    width: calc((100% - 10vh)/2);
}

.Header-version {
  font-size: calc(2vmin);
}

.Header-author {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: calc((100% - 10vh)/2);
}

.Header-logo {
    color: #61dafb;
    height: 100%;
    width: 10vh;
  }

.Header-logo-image {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }

.Header-logo-link {
    display: block;
    height: 100%;
    width: 100%;
  }
  
@media (prefers-reduced-motion: no-preference) {
  .Header-logo-image {
      animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
} 
 